@import url('https://fonts.googleapis.com/css?family=Roboto:300');

.home-text {
  text-align:center;
  color:rgb(0, 0, 0);
  font-family:'Roboto';
  font-weight:800;
  font-size:20px;
  border: solid;
  top: 25%;
  left: 36%;
  overflow:hidden;
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000;
  -webkit-transform: translate3d(0,0,0);
  background-color: rgba(220, 228, 220, 0.3);
}


.home-text {
  display:inline-block;
  overflow:hidden;
  white-space:nowrap;
}

.home-text:first-of-type {    /* For increasing performance 
                       ID/Class should've been used. 
                       For a small demo 
                       it's okaish for now */
  animation: showup 7s infinite;
}

.home-text:last-of-type {
  width:0px;
  animation: reveal 7s infinite;
}

.home-text:last-of-type span {
  margin-left:-355px;
  animation: slidein 7s infinite;
}

@keyframes showup {
    0% {opacity:0;}
    20% {opacity:1;}
    80% {opacity:1;}
    100% {opacity:0;}
}

@keyframes slidein {
    0% { margin-left:-800px; }
    20% { margin-left:-800px; }
    35% { margin-left:0px; }
    100% { margin-left:0px; }
}

@keyframes reveal {
    0% {opacity:0;width:0px;}
    20% {opacity:1;width:0px;}
    30% {width:455px;}
    80% {opacity:1;}
    100% {opacity:0;width:375px;}
}
